body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.c-letter-tile {
  display: inline-block;
  font-weight: bold;
  -webkit-text-decoration: uppercase;
          text-decoration: uppercase;
  font-size: 20px;
  height: 32px;
  width: 32px;
  margin: 3px 5px;
}

@media (min-width: 768px) {
  .c-letter-tile {
    height: 40px;
    width: 40px;
    margin: 5px 8px;
    font-size: 28px;
  }
}

.c-letter-tile:active {
  position: relative;
  top: 1px;
}

.c-letter-tile:disabled,
.c-letter-tile:disabled:hover,
.c-letter-tile:disabled:focus {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #fc8d83), to(#e4685d));
  background: -webkit-linear-gradient(top, #fc8d83 5%, #e4685d 100%);
  background: linear-gradient(to bottom, #fc8d83 5%, #e4685d 100%);
  background-color: #fc8d83;
  border: 1px solid #d83526;
}

.c-letter-rack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media (min-width: 768px) {
  .c-letter-rack {
    max-width: 500px;
  }
}

svg {
  stroke: #000;
  stroke-width: 3;
  stroke-linecap: round;
}

circle {
  fill: none;
}

.c-question {
  font-size: 20px;
  width: 50%;
  display: flex;
  align-items: center;
  text-align: center;
}

@media (min-width: 768px) {
  .c-question {
    font-size: 40px;
  }
}

.c-answer-letter {
  border-bottom: 2px solid black;
  width: 30px;
  font-size: 30px;
  margin-right: 10px;
  display: inline-block;
  text-align: center;
  min-height: 40px;
  vertical-align: top;
}

@media (min-width: 768px) {
  .c-answer-letter {
    width: 50px;
    font-size: 50px;
    min-height: 60px;
  }
}

.c-answer-letter:last-child {
  margin-right: 0px;
}

.c-answer-word {
  margin: 0 20px 20px;
}

@media (min-width: 768px) {
  .c-answer-word {
    margin-right: 40px;
  }
}

.c-answer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.c-dimmer {
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #000;
  opacity: 0.5;
  height: 100%;
  width: 100%;
}

.c-modal {
  position: absolute;
  bottom: 15px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 200px;
  width: 90%;
  background-color: #fff;
  padding: 15px;
  border-radius: 10px;
}

@media (min-width: 768px) {
  .c-modal {
    width: 400px;
  }
}

.c-close {
  position: absolute;
  right: 0;
  top: 0;
}

#hangman-icon {
  width: 50%;
}

@media (min-width: 768px) {
  #hangman-icon {
    height: 200px;
  }
}

.c-game {
  display: flex;
  flex-wrap: wrap;
  margin: 0 15px;
  min-height: 90%;
  justify-content: center;
  max-width: 1000px;
}

.c-game > * {
  flex-grow: 1;
}

.c-title {
  width: 100%;
  text-align: center;
  margin-bottom: 15px;
}

@media (min-width: 768px) {
  .c-title {
    font-size: 40px;
  }
}

.btn {
  box-shadow: 0px -1px 0px 0px #f0f7fa;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #33bdef), to(#019ad2));
  background: -webkit-linear-gradient(top, #33bdef 5%, #019ad2 100%);
  background: linear-gradient(to bottom, #33bdef 5%, #019ad2 100%);
  background-color: #33bdef;
  border-radius: 6px;
  border: 1px solid #057fd0;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-weight: bold;
  text-shadow: 0px -2px 0px #5b6178;
}

.btn:hover,
.btn:focus {
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, #019ad2), to(#33bdef));
  background: -webkit-linear-gradient(top, #019ad2 5%, #33bdef 100%);
  background: linear-gradient(to bottom, #019ad2 5%, #33bdef 100%);
  background-color: #019ad2;
  outline: none;
}

.c-game-over-buttons .btn {
  height: 32px;
  font-size: 20px;
  margin: 0 15px;
}

.c-game-over > * {
  width: 100%;
  text-align: center;
  font-size: 26px;
}

.c-game-over {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

/* App.css */
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

